import React, { Component } from 'react';
import SpotifyPlayer from 'react-spotify-player';
import './audioPlayer.css';
import ReactAudioPlayer from 'react-audio-player';
import song from '../assets/audio/Seaweed.ogg';
import ReactPlayer from 'react-player';
import AudioPlayer from "react-h5-audio-player";
import song1 from '../assets/audio/Seaweed.ogg';
import song2 from '../assets/audio/screwYou.ogg';

import bunny from '../assets/audio/bunny.ogg';
import { FaPlay, FaPause, FaForward, FaBackward } from 'react-icons/fa';
class AudioPlayerComponent extends Component {



constructor(props) {
  super(props);


  
    this.timeUp = this.timeUp.bind(this);
    this.playSong = this.playSong.bind(this);
    this.updateTime = this.updateTime.bind(this);

  }

  state = {
    songPlaying: false,
    playIconDisplay: "inline",
    pauseIconDisplay: "none",
    currentSong:0,
    audioCurrentTime:0,
    songs:[
      song2,
    ],
  titles: [
    "01: Screw You"
  ]
}

  timeUp = () => {
    let audio = document.getElementById('musicPlayer');
    if (this.state.songPlaying==true) {
      console.log("playing")
      this.interval = setInterval(() => {

        this.setState({
          audioCurrentTime:  (audio.currentTime/audio.duration *100) + '%'
        })
        // this.audioCurrentTime =  Math.floor(audio.currentTime/audio.duration *100) + '%';
        console.log("Hello", this.state.audioCurrentTime);


      }, 500);

    } else {
      console.log("stopping");
      clearInterval(this.interval);



    }

    console.log("geee", audio);
    // document.getElementById('tracktime').innerHTML =
    //  Math.floor(audio.currentTime) + ' / ' + Math.floor(audio.duration);
  }

  updateTime = () => {
    console.log("its time to change1",this)
  }

 playSong = () => {
   let audio = document.getElementById('musicPlayer');


  if (this.state.songPlaying == false) {
    audio.play();

    this.setState({songPlaying: true,
                  playIconDisplay: "none",
                  pauseIconDisplay: "inline"
                }, () => {
                  this.timeUp();
                })


    console.log("state",this.state)
      } else {

    audio.pause();
    this.setState({songPlaying: false,
                  playIconDisplay: "inline",
                  pauseIconDisplay: "none"
                  }, () => {
                    this.timeUp();
                  })
      }
  }

  previousSong = () => {
    let audio = document.getElementById('musicPlayer');
    //safe indexing check for currentSong
    if (this.state.currentSong == 0) {
      console.log("uh oh ")
      var newSongIndex = this.state.songs.length -1;
    } else {
      var newSongIndex = this.state.currentSong-1
    }

    audio.pause();

    this.setState({
        currentSong: newSongIndex,
        songPlaying: true,
        playIconDisplay: "none",
        pauseIconDisplay: "inline"
    }, () => {
      console.log("hallo",this.state)
      audio.setAttribute('src',this.state.songs[this.state.currentSong]);
      audio.load()
      audio.play()
    })
  }

  nextSong = () => {
    let audio = document.getElementById('musicPlayer');
    //safe indexing check for currentSong
    if (this.state.currentSong+1 == this.state.songs.length) {
      var newSongIndex = 0;
    } else {
      var newSongIndex = this.state.currentSong+1
    }

    audio.pause();

    this.setState({
        currentSong: newSongIndex,
        songPlaying: true,
        playIconDisplay: "none",
        pauseIconDisplay: "inline"
    }, () => {
      audio.setAttribute('src',this.state.songs[this.state.currentSong]);
      audio.load()
      audio.play()
    })
  }

  render() {
    const size = {
  width: '100%',
  height: '100%',

  };

  const playStyle = {
    display: this.state.playIconDisplay
  }

  const pauseStyle = {
    display: this.state.pauseIconDisplay
  }
  const audioPosition = {
    width: this.state.audioCurrentTime
  }


    return (
        <div id="audioControlPanel">

            <button className="songChange" id="previousSong" onClick={this.previousSong} ref={this.song}>
            <FaBackward size="15px" color="white" id="play" />
            </button>

            <button id="playButton" onClick={this.playSong} ref={this.song}>
                <FaPlay size="18px" color="white" style={playStyle} id="play" />
                <FaPause size="18px" color="white" style={pauseStyle} id="pause" />
            </button>

            <button className="songChange" id="nextSong" onClick={this.nextSong} ref={this.song}>
                <FaForward size="15px" color="white"  />
            </button>

            <div id="durationView"  onClick={this.updateTime}>
                <div id="durationPosition" style={audioPosition}></div>
            </div>

            <h4 id="songTitle">{this.state.titles[this.state.currentSong]}</h4>
            <ReactAudioPlayer src= {this.state.songs[this.state.currentSong]} type= 'video/ogg' id="musicPlayer" />
        </div>
    );
  }
}



export default AudioPlayerComponent;
